.rendimientoClientes {
  display: flex;
  flex-direction: column;
  gap: 24px;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  min-height: 0;
}

.topClientsCard,
.churnRiskCard {
  background: var(--surface-primary);
  border-radius: var(--radius-lg);
  padding: 24px;
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--border-primary);
  transition: background-color 0.3s ease, border-color 0.3s ease;
}

.cardTitle {
  font-family: var(--font-display);
  font-size: 1.5rem;
  font-weight: 400;
  color: var(--text-primary);
  margin-bottom: 8px;
}

.cardSubtitle {
  font-size: 0.875rem;
  color: var(--text-tertiary);
  margin-bottom: 20px;
}

.barChart {
  display: flex;
  gap: 16px;
  align-items: flex-end;
  height: 300px;
  padding: 20px 0;
  margin-top: 20px;
}

.barGroup {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}

.barContainer {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}

.bar {
  width: 80%;
  background: linear-gradient(180deg, var(--azure-light), var(--azure));
  border-radius: var(--radius-sm) var(--radius-sm) 0 0;
  min-height: 20px;
  transition: all 0.3s;
  cursor: pointer;
}

.bar:hover {
  opacity: 0.9;
  transform: scaleY(1.05);
}

.barLabel {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;
  width: 100%;
}

.clientName {
  font-size: 0.75rem;
  font-weight: 500;
  color: var(--text-primary);
  text-align: center;
  word-break: break-word;
}

.clientValue {
  font-size: 0.7rem;
  color: var(--text-tertiary);
  font-weight: 600;
}

.tableContainer {
  overflow-x: auto;
  margin-top: 20px;
}

.daysToPay {
  font-weight: 600;
  padding: 4px 8px;
  border-radius: var(--radius-sm);
}

.daysToPay.mediumRisk {
  color: var(--warning);
  background: var(--warning-bg);
}

.daysToPay.highRisk {
  color: var(--danger);
  background: var(--danger-bg);
}

.lifetimeValue {
  font-weight: 600;
  color: var(--text-primary);
}

